import React from 'react';
import {Panel, Div, Button, Cell, Avatar, ModalRoot, ModalPage, ModalPageHeader} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import VKStories from "vk-stories";
import connect from "@vkontakte/vkui-connect-promise";
VKStories.init(connect);




//компонент модального окна
class Modal extends React.Component {
  constructor(props) {
      super(props);
  }

    ReturnMap = () => {
      this.props.theStory();
    }

  GenerateStory = () => {
      VKStories.generateStoryFromTemplate(require("../img/Design-story.png"), [
      {
        font: "45px Arial",
        align: "center",
        color: "#FFFFFF",
        value: "ТЕСТ ИСТОРИИ",
        x: 540,
        y: 1133,
        }
     ])
     .then( (story) => {
        VKStories.shareStory(7150376, story, { add_to_news: true })
        this.ReturnMap();
    })
}
     

   

  render() {
    return(
      <ModalRoot activeModal="modalStory">
         <ModalPage id="modalStory">
           <ModalPageHeader>Истории</ModalPageHeader>
             <Button level="commerce" size="xl" onclick={this.GenerateStory}>Опубликовать</Button>
            <Cell>gsdgdsgdss
            gsdgdsgdssgdg
            dgdgdsgsdgsgvccvcvcv
            cvcvcvc
            vcvcvcv
            cvcvcvccvcvcvc
            cvcvcvccvcvcvcccvcv
            vvcvcvcv</Cell>
         </ModalPage>
      </ModalRoot>
   );
  }

}

export default Modal;
